.appBar {
  display: flex;
  height: 6.4rem;
  align-items: center;
  padding: 0 2.4rem;
  color: white;
  background: #303f9f; }
  @media screen and (max-width: 480px) and (orientation: portrait) {
    .appBar {
      height: 5.6rem; } }
  @media screen and (max-width: 600px) and (orientation: landscape) {
    .appBar {
      height: 4.8rem; } }
  .appBar:not(.flat) {
    z-index: 100;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26); }
  .appBar.fixed {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 300; }
  .appBar a {
    color: white; }



